import React from 'react';
import PropTypes from 'prop-types';
import {ListItem, Group, Avatar} from '@vkontakte/vkui';

const  User = ({fetchedUser}) => {
	const {
		photo_200: photo,
		city: { title = '' },
		first_name,
		last_name
	} = fetchedUser;
	return (
		<Group title="">
			<ListItem
				before={photo ? <Avatar src={photo}/> : null}
				description={title}
			>
				{`${first_name} ${last_name}`}
			</ListItem>
		</Group>
	);
}

User.propTypes = {
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		photo_200_orig: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		})
	})
};

export default User;